import './AdminClienteDetail.css'

import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import { MapContainer, Marker, TileLayer } from 'react-leaflet'
import { RouteComponentProps } from 'react-router'

import { Page } from '../../components'
import { useStores } from '../../models/root-store/root-store-context'

const moment = require('moment-timezone')

const defaultValues = {
  tipo: 0,
  usuario: '',
  fabricante: '',
  orden: 0,
  fecha_hora_calculada_desde: '',
  direccion: '',
  ubicacion: '',
  distancia: '',
  tiempo_en_transito: 0,
  finalizado: false,
  pedidos: [
    {
      referencia: '',
      usuario: {
        first_name: '',
        last_name: '',
      },
      productos: [
        {
          producto: {
            imagenes: [{ imagen: '' }],
            precio_mayorista: 0,
            precio: 0,
            nombre: '',
          },
          compra_mejorada: false,
          cantidad: 0,
        },
      ],
    },
  ],
}

export const AdminHojaRutaItinerario: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { adminStore } = rootStore
  const [Ready, setReady] = useState(false)
  const [Center, setCenter] = useState({ lng: -57.63591, lat: -25.30066 })
  const [IdHojaRuta, setIdHojaRuta] = useState(0)
  const [Itinerario, setItinerario] = useState(defaultValues)

  const setup = () => {
    setReady(true)
    const idHojaRuta = history.location.pathname.split('/')[4]
    setIdHojaRuta(parseInt(idHojaRuta))
    if (adminStore.itinerario) {
      // @ts-ignore
      setItinerario(adminStore.itinerario)
      let ubicacion = adminStore.itinerario.ubicacion.split(',')
      let lng = parseFloat(ubicacion[1])
      let lat = parseFloat(ubicacion[0])
      setCenter({ lng: lng, lat: lat })
    }
  }

  React.useEffect(() => {
    setup()
  }, [])

  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Itinerario {Itinerario.orden}</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item">
                  <a href="/admin/hoja_ruta">Hojas de ruta</a>
                </li>
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/hoja_ruta/detail/' + IdHojaRuta)}>Hoja de ruta {IdHojaRuta}</a>
                </li>
                <li className="breadcrumb-item active">Itinerario {Itinerario.orden}</li>
              </ol>
            </div>
          </div>
          <div className="card">
            <div className="card-body">
              <div className={'row mb-4'}>
                <div className={'col-3'}>
                  <strong>Tipo: </strong>
                  {Itinerario.tipo === 20 && 'Retiro'}
                  {Itinerario.tipo === 10 && 'Entrega'}
                </div>
                <div className={'col-3'}>
                  <strong>Cliente/Fabricante: </strong>
                  {Itinerario.usuario && Itinerario.usuario}
                  {!Itinerario.usuario && Itinerario.fabricante}
                </div>
                <div className={'col-3'}>
                  <strong>Fecha/hora: </strong>
                  {moment(Itinerario.fecha_hora_calculada_desde).format('DD/MM/YYYY HH:mm')}
                </div>
                <div className={'col-3'}>
                  <strong>Finalizado: </strong>
                  {Itinerario.finalizado ? 'Si' : 'No'}
                </div>
              </div>
              <div className={'row mb-3'}>
                <div className={'col-3'}>
                  <strong>Dirección: </strong>
                  {Itinerario.direccion}
                </div>
                <div className={'col-3'}>
                  <strong>Distancia: </strong>
                  {Itinerario.distancia} km.
                </div>
                <div className={'col-3'}>
                  <strong>Tiempo: </strong>
                  {Itinerario.tiempo_en_transito} min.
                </div>
              </div>
            </div>
          </div>
          <h4>Pedidos</h4>
          <div className="card">
            <div className="card-body m-0 p-0 table-responsive">
              <table className={'table'}>
                <thead>
                  <tr>
                    <th>Pedido</th>
                    <th>Cliente</th>
                  </tr>
                </thead>

                {Itinerario.pedidos.map((pedido) => {
                  return (
                    <tbody>
                      <tr>
                        <td>#{pedido.referencia}</td>
                        <td>
                          {pedido.usuario.first_name} {pedido.usuario.last_name}
                        </td>
                      </tr>
                      <tr>
                        <td></td>
                        <td className={'p-0'}>
                          <table>
                            <thead>
                              <th>Cantidad</th>
                              <th>Nombre</th>
                            </thead>
                            {pedido.productos.map((producto) => {
                              return (
                                <tbody>
                                  <tr>
                                    <td>{producto.cantidad}</td>
                                    <td>{producto.producto.nombre}</td>
                                  </tr>
                                </tbody>
                              )
                            })}
                          </table>
                        </td>
                      </tr>
                    </tbody>
                  )
                })}
              </table>
            </div>
          </div>
          <h4>Recorrido</h4>
          <div style={{ height: 300, width: '100%' }} className={'mb-4'}>
            {Ready && (
              <MapContainer center={Center} zoom={12} style={{ height: 300, width: '100%' }}>
                <TileLayer attribution="" url="https://{s}.tile.osm.org/{z}/{x}/{y}.png" />
                <Marker position={{ lat: Center.lat, lng: Center.lng }}></Marker>
              </MapContainer>
            )}
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminHojaRutaItinerario
